import React,{ useState } from "react";
import Alert from './Alert';
import logo1 from './colors2.jpg'

export default function TextForm() {
    const [text,setText] = useState("");
    const [alert,setAlert] = useState("");
    
    //  CSS  for Color Modes
    // const color1 = {
    //     buttonClr:"3D405B",
    //     backgroundClr:"E07A5F"
    // }
    const color2 = {
        buttonClr:"95AFBA",
        backgroundClr:"3F7CAC"
    }

    const changeTheme = (theme)=>{
        console.log(theme)
        document.querySelectorAll("button").forEach((e)=>{
            if(e.type ==="submit" || e.type ==="button"){
                // console.log(e)
                e.style.backgroundColor = "#"+theme.buttonClr ;
                e.style.backgroundColor = "#"+theme.buttonClr ;
            }
        })

        document.getElementsByClassName("containe")[0].style.background = "#"+theme.backgroundClr
        document.getElementsByClassName("containe")[1].style.background = "#"+theme.backgroundClr
        document.getElementById("textArea").style.background = "#"+theme.backgroundClr
        document.body.style.color = "#81B29A"
    }
    
    






    const ShowAlert= (message)=>{
        setAlert(message)
        setTimeout(()=>{
            setAlert("")
        },1500)
    }


    let convertToUC = ()=>{
        ShowAlert({
            msg : "Converted to Uppercase",
            type:"warning"
        })
        setText(text.toUpperCase())
        console.log("Converting to Uppercase")
        
    }

    const convertToLC = ()=>{
        ShowAlert({
            msg : "Converted to Lowercase",
            type:"warning"
        })
        setText(text.toLowerCase())
        console.log("converted to Lowercase.")
    }

    let changeEventHandle = (event)=>{
        setText(event.target.value)
    }
    let boldText = ()=>{
        ShowAlert({
            msg : "Text is Bolded",
            type:"warning"
        })
        if(document.getElementById("textArea").style.fontWeight === "bolder"){
            document.getElementById("textArea").style.fontWeight = "normal"
            return
        }
            document.getElementById("textArea").style.fontWeight = "bolder"
    }
    
    let emText = ()=>{
        ShowAlert({
            msg : "Text is Emphasized",
            type:"warning"
        })
        if(document.getElementById("textArea").style.fontStyle === "italic"){
            document.getElementById("textArea").style.fontStyle = "normal"
            return
        }
            document.getElementById("textArea").style.fontStyle = "italic"
    }
    function clearText(){
       setText("")
       ShowAlert({
        msg : "Text is Cleared",
        type:"danger"
    })
    }
    let copyText = ()=>{
        ShowAlert({
            msg : "Text is Copied",
            type:"warning"
        })
        navigator.clipboard.writeText(text)
        document.getElementById("info").innerHTML = "Text copied successfully."
        setTimeout(()=>{
            document.getElementById("info").innerHTML =""
            
        },2000)
    }

    const [btnText, setBtnText] = useState("Set Dark Mode")
    const [myStyle, setMyStyle] = useState({
        color:"black",
        background:"white",
        padding: "22px 45px"
    })

    function DarkMode(){
        if( btnText === "Set Light Mode"){
            setBtnText("Set Dark Mode")
            setMyStyle({
                color:"black",
                background:"white",
                padding: "22px 45px"
            })
            ShowAlert({
                msg : "Light Mode Enabled",
                type:"dark"
            })
         
        }
        else{
            setBtnText ("Set Light Mode")
            setMyStyle({
                color:"white",
                background:"black",
                padding: "22px 45px"
            })
            ShowAlert({
                msg : "Dark Mode Enabled",
                type:"info"
            })
        }
    }
    const myStyle2 = {
       width:"100%"
    }


  

    return (
        <>
        <Alert alert = {alert}/>
        
      
         <img  onClick={()=>{changeTheme(color2)}} src = {logo1} alt=""  />
    
        <div style={{...myStyle , ...myStyle2}}  className="containe py-0" >
        <button type="button my" style={{marginTop:"25px"}} className="btn btn-primary" onClick={DarkMode} >{btnText}</button>
                <div className="mb-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">ENTER TEXT HERE</label>
                    <textarea  style={myStyle}  autoComplete="true" id="textArea" value={text} placeholder="Enter Text here" onChange={changeEventHandle} type="text" rows={8} className="form-control" ></textarea>
                </div>
                <small id="info"></small><br/>
                <h3>Convert text to</h3>
                <button type="submit" onClick={clearText} className="btn btn-success mx-2 my-1">CLEsAR TEXT</button>
                <button type="submit" onClick={convertToUC} className="btn btn-success mx-2 my-1">UPPERCASE</button>
                <button type="submit" onClick={convertToLC} className="btn btn-success mx-2 my-1">LOWERCASE</button>
                <button type="submit" onClick={boldText} className="btn btn-success mx-2 my-1">BOLD TEXT</button>
                <button type="submit" onClick={emText} className="btn btn-success mx-2 my-1">EMPHASIZE TEXT</button>
                <button type="submit" onClick={copyText} className="btn btn-success mx-2 my-1">COPY TEXT</button>
         
        </div>
        <div style={{...myStyle , ...myStyle2}} className="containe py-5">
            <h2><u> More information </u></h2>
            <small>Total words : {text.split(" ").length} </small> <br/>
            <small>Total Characters : {text.length} </small>
        </div>


        </>
    )

    
   
}